/*
 *  Callout CTA
 *    Styles for the callout cta section.
 *
 *  Version:
 *    1.0.0 - 2021/11/10
 */

.c-callout-cta {
  background-color: $s-color--background-impact;
  position: relative;
  padding: 4rem 4rem 9rem;


  &__graphic-content {
    display: none;
  }

  &__wrapper {
    max-width: 25rem;
    margin: 0 auto;
    text-align: center;
  }

  &__title {
    margin-bottom: 1.5625rem;
    color: $s-color--font-invert;
  }

  &__description {
    margin-bottom: 2.25rem;
    color: $s-color--font-invert;
    text-align: center;
  }

  &__btn {
    min-width: 15.4375rem;
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    padding: 9.7125rem 4% 8.025rem;

    &__inner {
      display: grid;
      grid-template-columns: 49.75% 34.5%;
      gap: 15.75%;
      width: #{'min(75%, 70.3125rem)'};
      margin: 0 auto;
    }

    &__graphic-content {
      display: inline-block;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        width: 24.5625rem;
        height: 23.375rem;
        top: -8.375rem;
        left: -10.375rem;
        background-image: url(./../../assets/svg.ovals.svg);
        background-repeat: no-repeat;
        background-size: cover;
      }
    }

    &__image {
      &-container {
        padding-bottom: 82.32%;
      }
    }

    &__wrapper {
      margin-top: 2.325rem;
      text-align: left;
    }

    &__title {
      max-width: 26.25rem;
      margin-bottom: 1.125rem;
    }

    &__description {
      max-width: 25.125rem;
      margin-bottom: 2.5rem;
      text-align: left;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 16.1875rem 4% 13.375rem;

    &__inner {
      max-width: 72.1875rem;
    }

    &__graphic-content {
      &::before {
        width: 32.75rem;
        height: 31.3125rem;
        top: -7.75rem;
        left: -13.4375rem;
      }
    }

    &__wrapper {
      margin-top: 3.875rem;
    }

    &__btn {
      min-width: 16.25rem;
    }
  }
}